import { Box, Button, Menu, MenuButton, MenuList, Text, Tooltip,Avatar, MenuItem, MenuGroup, Drawer, DrawerOverlay, DrawerHeader, DrawerContent, DrawerBody, Input, useToast, Spinner } from '@chakra-ui/react'
import React from 'react'
 import { useState } from 'react' 
import {BellIcon,ChevronDownIcon} from '@chakra-ui/icons'
import { ChatState } from '../../context/ChatProvider'
import ProfileModel from './ProfileModel'
import {  useNavigate } from 'react-router-dom'
import { useDisclosure } from '@chakra-ui/react'
import { ChatLoading } from './ChatLoading'
import UserListItem from './UserListItem'
import {getSender} from '../../ChatLogic'
import NotificationBadge from 'react-notification-badge';
import {Effect} from 'react-notification-badge';


export default function SideDrawer() {
  const [search, setsearch] = useState("")
  const [loading, setloading] = useState(false)
  const [searchresult, setsearchresult] = useState([])
   const [loadingchat, setloadingchat] = useState(false) 

  const {user,setselectedchat,chats,setchats,setuser,notification,setnotification} = ChatState()
  const navigate = useNavigate()
  const { isOpen, onOpen, onClose } = useDisclosure()
  const toast = useToast()

  const handleLogout = async ()=>{
    localStorage.removeItem("userInfo")
    setuser("")
    navigate("/");
  }
  const  handleSearch =async ()=>{
    if(search===""){
      toast({
        title: 'Please Enter something!',
        status: 'warning',
        duration: 3000,
        isClosable: true,
        position:'top-left'
      })
      return
    }

    try {
      setloading(true)

        const data = await fetch(`/api/user?search=${search}`,{
          method:"GET",
          headers:{
            authorization:`Bearer ${user.token}`,
            'Accept': 'application/json',
            
            'Content-Type': 'application/json'
          },
          
      })

      const data1 = await data.json()
        setloading(false)
        setsearchresult(data1)
    } catch (error) {
      toast({
        title: 'Error Occured!',
        description:"Failed to Load the Search Results",
        status: 'error',
        duration: 3000,
        isClosable: true,
        position:'bottom-left'
      })
    }
  }
  const accessChat = async (userId)=>{
      try {
        setloadingchat(true)

        const data1 = await fetch('/api/chat',{
            method:'POST',
            body: JSON.stringify({userId}),
            headers:{
              authorization:`Bearer ${user.token}`,
              'Accept': 'application/json',
              
              'Content-Type': 'application/json'
            },
            
            
        })
        const data = await data1.json()
        if(!chats.find((c)=> c._id===data._id)) setchats([data,...chats])
    
        setselectedchat(data)
        
        setloadingchat(false)
        onClose()
      } catch (error) {
        toast({
          title: 'Error fetching the chat!',
          description:error.message,
          status: 'error',
          duration: 3000,
          isClosable: true,
          position:'bottom-left'
        })
      }
  }
        
  return (
    <>
    
    <Box
    display="flex" justifyContent="space-between" alignItems="center" bg="white" w="100%" p="5px 10px 5px 10px" borderWidth="5px" >
      <Tooltip label="Search Users to chat" hasArrow placement='bottom-end'>

      <Button variant="ghost" onClick={()=>{
        setsearchresult([])
        onOpen()
        }}>
      <i className="fa fa-search" aria-hidden="true"></i>
      <Text display={{base:"none",md:"flex"}} padding="3">Search User</Text>
      </Button>
      </Tooltip>
      <Text fontSize="4x1" fontFamily="Work sans" >
        CHAT
      </Text>
      <div>
        <Menu>
          <MenuButton p={1}>
          <NotificationBadge count={notification.length} effect={Effect.SCALE}/>
        <BellIcon/>
          </MenuButton>
          <MenuList p={4}>
            {!notification.length? "No new Messages":notification.map((notif) => (
                <MenuItem
                  key={notif._id}
                  onClick={() => {
                    setselectedchat(notif.chat);
                    setnotification(notification.filter((n) => n !== notif));
                  }}
                >
                  {notif.chat.isGroupChat
                    ? `New Message in ${notif.chat.chatName}`
                    : `New Message from ${getSender(user, notif.chat.users)}`}
                </MenuItem>
              ))}

          </MenuList>
        </Menu>
        <Menu>
          <MenuButton as={Button} rightIcon={<ChevronDownIcon/>} p={1}>
       <Avatar size='sm' cursor="pointer" name={user.user.name} src={user.user.picture}/>
       
          </MenuButton>
          <MenuList>
            <ProfileModel user={user.user}>
            <MenuItem>My Profile</MenuItem>
            </ProfileModel>
            
            <MenuGroup/>
            <MenuItem onClick={handleLogout} >Logout</MenuItem>
          </MenuList>
        </Menu>
      </div>
      
    </Box>
    
    <Drawer placement='left' onClose={onClose} isOpen={isOpen}>
       <DrawerOverlay/>
       <DrawerContent>
       <DrawerHeader borderBottomWidth="1px">Search Users</DrawerHeader>

       <DrawerBody>
          <Box display="flex" pd={2}>
              <Input placeholder='Search by name or email' mr={2} value={search} onChange={(e)=> setsearch(e.target.value)}/>
          
          <Button onClick={handleSearch}>Go</Button>
          </Box>
          {loading?<ChatLoading/>:
            searchresult?.map((user)=>
              <UserListItem
              key={user._id}
              user = {user}
              handleFunction={()=>accessChat(user._id)}
              />
            
          )

    }
          {loadingchat && <Spinner ml="auto" display="flex"/>}
        </DrawerBody>

       </DrawerContent>
        
    </Drawer>
    </>
  )
}
