import React from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  Box,
  FormControl,
  Input,
  useDisclosure,
  useToast,
} from "@chakra-ui/react";
import { useState } from "react";
import { ChatState } from "../../context/ChatProvider";
import UserListItem from "./UserListItem";
import UserBadgeItem from "./UserBadgeItem";

export default function GropChatModal({ children }) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [groupchatname, setgroupchatname] = useState();
  const [selectedusers, setselectedusers] = useState([]);
  const [search, setsearch] = useState("");
  const [searchresult, setsearchresult] = useState([]);
  const [loading, setloading] = useState(false);

  const toast = useToast();

  const { user, chats, setchats } = ChatState();

  const handleSearch = async (query) => {
    setsearch(query);
    if (!query) {
      return;
    }
    try {
      setloading(true);
      const dat = await fetch(`/api/user?search=${search}`, {
        method: "GET",
        headers: {
          authorization: `Bearer ${user.token}`,
          Accept: "application/json",

          "Content-Type": "application/json",
        },
      });
      const data = await dat.json();
     
      setloading(false);
      setsearchresult(data);
    } catch (error) {
        toast({
            title: "Error Occured!",
            description: "Failed to load Search Results",
            status: "error",
            duration: 3000,
            isClosable: true,
            position: "bottom-left",
          });
    }
  };

  const handleSubmit = async () => {
    if(!groupchatname || !selectedusers){
        toast({
            title: "Please fill all the feilds",
            
            status: "warning",
            duration: 3000,
            isClosable: true,
            position: "top",
          });
        return
    }
    try { 
        const temp =[]
        selectedusers.map((user)=>(temp.push(user._id)))
        const dat = await fetch("/api/chat/group", {
            method: "POST",
            body:JSON.stringify({
                name:groupchatname,
                users:temp
            }),
            headers: {
              authorization: `Bearer ${user.token}`,
              Accept: "application/json",
    
              "Content-Type": "application/json",
            },
          });
          const data = await dat.json()
          setchats([data,...chats])
       
          onClose()
          toast({
            title: "New Group Chat Created",
            
            status: "success",
            duration: 3000,
            isClosable: true,
            position: "bottom",
          });
    } catch (error) {
        toast({
            title: "Please to create the Chat",
            description:error.response.data,
            status: "warning",
            duration: 3000,
            isClosable: true,
            position: "bottom",
          });
    }
  };

  const handleGroup = (userToAdd)=>{
    if(selectedusers.includes(userToAdd)){
        toast({
            title: "User already added",
            
            status: "warning",
            duration: 3000,
            isClosable: true,
            position: "bottom-left",
          });
        return
    }
    
    setselectedusers([...selectedusers,userToAdd])
  }

  const handleDelete = (userToDelete)=>{
    setselectedusers(selectedusers.filter(sel=> sel._id!==userToDelete._id))
  }

  const handleOpen = ()=>{
    setsearchresult([])
    onOpen()
  }

  return (
    <>
      <span onClick={handleOpen}>{children}</span>

      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader
            fontSize="35px"
            fontFamily="Work sans"
            display="flex"
            justifyContent="center"
          >
            Create Group Chat
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody display="flex" justifyContent="center" flexDir="column">
            <FormControl>
              <Input
                placeholder="Chat Name"
                mb={3}
                
                onChange={(e) => setgroupchatname(e.target.value)}
              />
            </FormControl>
            <FormControl>
              <Input
                placeholder="Add users eg:John,Jane"
                mb={1}
                
                onChange={(e) => handleSearch(e.target.value)}
              />
            </FormControl>

            <Box display="flex" flexWrap="wrap" >
            {selectedusers.map(user=>(<UserBadgeItem key={user._id} user={user} handleFunction={()=>handleDelete(user)} />))}
            </Box>

            {loading?<div>loading</div>:(searchresult.slice(0,4).map(user=>(<UserListItem key={user._id} user={user}handleFunction={()=>handleGroup(user)}/>)))}

          </ModalBody>

          <ModalFooter>
            <Button colorScheme="blue" onClick={handleSubmit}>
              Create chat
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}
