import React from 'react'
import {
    
    IconButton,
    Image,
    Text,
    useDisclosure,
    
  } from '@chakra-ui/react'
import { ViewIcon } from '@chakra-ui/icons'
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    Button,
  } from '@chakra-ui/react'

export default function ProfileModel({user,children}) {
    const { isOpen, onOpen, onClose } = useDisclosure()
    return (
      <>
        {children?<span onClick={onOpen}>{children}</span>:<IconButton display={{base:"flex"}} icon={<ViewIcon/>} onClick={onOpen}></IconButton> }

        <Modal isOpen={isOpen} onClose={onClose} size="lg" isCentered >
        <ModalOverlay />
        <ModalContent h="400px">
          <ModalHeader fontSize="40px" fontFamily="Work sans" display="flex" justifyContent="center" >{user.name}</ModalHeader>
          <ModalCloseButton />
          <ModalBody display="flex" flexDir="column" alignItems="center" justifyContent="space-between" >
            <Image borderRadius="full" boxSize="150px" src={user.picture} alt={user.name} >
            </Image>
            <Text fontWeight= 'bold'>Email:{user.email}</Text>
          </ModalBody>

          <ModalFooter>
            <Button colorScheme='blue' mr={3} onClick={onClose}>
              Close
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
      </>
    )
}
