import React,{ useState } from "react";
import { VStack, FormControl, FormLabel, Input, InputGroup, InputRightElement ,Button,useToast} from "@chakra-ui/react";
import {useNavigate} from 'react-router-dom';

export default function Login() {
  /*state*/
  const [show, setshow] = useState(false)
  const [password, setpassword] = useState('')
  const [email, setemail] = useState('')
  const [loading, setloading] = useState(false)
  const toast = useToast()
 const Navigate = useNavigate()

  /*functions*/
  const handleshow = ()=>{setshow(!show);}
  const submitHandler = async ()=>{
    setloading(true);
    if( !email || !password)
{
  toast({
    title: 'Please fill all the feilds!',
    status: 'warning',
    duration: 5000,
    isClosable: true,
    position:'bottom'
  })
  setloading(!true);
  return
}
    try {
      const data = await fetch("/api/user/login",{
        method:'POST',
        body:JSON.stringify({email,password}),
        headers:{
          'Accept': 'application/json',
          
          'Content-Type': 'application/json'
        }
      })
      
      const value = await data.json()
      toast({
        title: 'login Sucessful',
        status: 'success',
        duration: 5000,
        isClosable: true,
        position:'bottom'
      })
      localStorage.setItem("userInfo",JSON.stringify(value) )
      setloading(false)
      
        Navigate("/chats")
     
      
     /*  console.log("hi"); */
      /* window.location.reload()
      console.log("bye"); */
    } catch (error) {
      toast({
        title: 'error',
        status: 'error',
        duration: 5000,
        isClosable: true,
        position:'bottom'
      })
    }
    setloading(false)
  }
  
  return <VStack spacing={2}>
    <FormControl isRequired id='email'>
      <FormLabel fontSize='sm'>Email</FormLabel>
      <Input type={'email'} placeholder="Enter Your Email" onChange={(e)=>{setemail(e.target.value)}} value={email}/>
    </FormControl>
    <FormControl isRequired id='password'>
      <FormLabel fontSize='sm'>Password</FormLabel>
      <InputGroup>
      <Input type={show?'text':'password'} placeholder="Enter Your password" onChange={(e)=>{setpassword(e.target.value)}} value={password}/>
      <InputRightElement width="4.5rem">
        <Button h="1.75rem" size='sm' onClick={handleshow}>
          {show?'Hide':'Show'}
        </Button>
      </InputRightElement>
      </InputGroup>
    </FormControl>  

    <Button colorScheme='teal' width='100%' style={{marginTop:'20px',marginDown:'0px'}} 
      onClick={submitHandler} isLoading={loading}
  >Login
</Button>
<Button colorScheme='red' variant="solid" width='100%' style={{marginTop:'20px',marginDown:'0px'}} 
      onClick={()=>{
        setemail('guest@example.com')
        setpassword('123456')
      }}
  >Get Guest User Credentials
</Button>
    
  </VStack>;
  
}
