import { Avatar, Tooltip } from '@chakra-ui/react';
import React, { useEffect, useRef } from 'react'
import {isSameSender,isLastMessage,isSameSenderMargin,isSameUser} from "../../ChatLogic"
import {ChatState} from '../../context/ChatProvider'

const ScrollableChat = ({ messages }) => {

  const {user} = ChatState();
  const messagesEndRef = useRef(null)

  const scrollToBottom = () => {
    messagesEndRef.current.scrollIntoView({ behavior: "smooth" })
  }

   useEffect(scrollToBottom, [messages]); 

  return (
    <div>
      {messages.map((m,i)=>
      <div style={{display:"flex"}} key={m._id}>
        {(isSameSender(messages,m,i,user.user._id) || isLastMessage(messages,i,user.user._id)) && (
          
          <Tooltip label={m.sender.name} placement="bottom-start" hasArrow>
            
            <Avatar mt="7px" mr={1} size="sm" cursor="pointer" name ={m.sender.name} src={m.sender.picture}/>
          </Tooltip>
        )
        }
        <span style={{backgroundColor:`${m.sender._id===user.user._id?"#BEE3F8":"#B9F5D0"}`, borderRadius:"20px" , padding:"5px 15px" , maxWidth:"75%",marginTop:isSameUser(messages,m,i,user.user._id)?4:10, marginLeft:isSameSenderMargin(messages,m,i,user.user._id)}}>{m.content}</span>
      </div>
      )}
      <div ref={messagesEndRef} />
    </div>
  )
}

export default ScrollableChat