import { Box, Button, Stack, Text, useToast } from "@chakra-ui/react";
import React from "react";
import { ChatState } from "../../context/ChatProvider";
import {  useEffect } from "react";
import {AddIcon} from "@chakra-ui/icons"
import { ChatLoading } from "./ChatLoading";
import { getSender } from "../../ChatLogic";
import GropChatModal from "./GroupChatModal";

export default function MyChats() {
  const { user, selectedchat, setselectedchat, chats, setchats,fetchagain } = ChatState();


  const toast = useToast();

  const fetchChats = async () => {
    try {
      const data = await fetch("/api/chat", {
        method: "GET",
        headers: {
          authorization: `Bearer ${user.token}`,
          Accept: "application/json",

          "Content-Type": "application/json",
        },
      });
      const data1 = await data.json()
      
      setchats(data1);
    } catch (error) {
      toast({
        title: "Error Occured!",
        description: "Failed to load chats",
        status: "error",
        duration: 3000,
        isClosable: true,
        position: "bottom-left",
      });
    }
  };
  

  useEffect(() => {
    fetchChats();
  }, [fetchagain]);

  
  

  return (
    <Box
      display={{ base: selectedchat ? "none" : "flex", md: "flex" }}
      flexDir="column"
      alignItems="center"
      p={3}
      bg="white"
      w={{ base: "100%", md: "31%" }}
      borderRadius="lg"
      borderWidth="1px"
    >
      <Box
        pd={3}
        px={3}
        fontSize={{ base: "28px", md: "30px" }}
        fontFamily="Work sans"
        display="flex"
        w="100%"
        justifyContent="space-between"
        alignItems="center"
      >My Chats
      <GropChatModal><Button display="flex" fontSize={{base:"17px",md:"10px",lg:"17px"}} rightIcon={<AddIcon/>}>New Group</Button></GropChatModal>
      
      </Box>

      <Box display="flex" flexDirection="column" p={3} bg="#F8F8F8" w="100%" h="100%" borderRadius="lg" overflowY="hidden"
      >
        {chats?<Stack overflowY="scroll">
            {chats.map((chat)=>(<Box onClick={()=>setselectedchat(chat)} cursor="pointer" bg={selectedchat===chat?"#38B2AC":"#E8E8E8"}
            color={selectedchat===chat?"    white":"black"} px={3} py={2} borderRadius="lg" key={chat._id}
            >
              
                <Text>{!chat.isGroupChat?getSender(user.user,chat.users):chat.chatName}</Text>
            </Box>))}
        </Stack>:<ChatLoading/>}
       
      </Box>

    </Box>
  );
}
