import React,{ useState } from "react";
import { VStack, FormControl, FormLabel, Input, InputGroup, InputRightElement ,Button} from "@chakra-ui/react";
import { useToast } from '@chakra-ui/react'
import { useNavigate } from "react-router-dom";

export default function Signup() {
  /*state*/
  const [conshow, setconshow] = useState(false)
  const [show, setshow] = useState(false)
  const [name, setname] = useState('');
  const [password, setpassword] = useState('')
  const [email, setemail] = useState('')
  const [confirmpassword, setconfirmpassword] = useState('')
  const [picture, setpicture] = useState(undefined)
  const [loading, setloading] = useState(false)
  const toast = useToast()
  const Navigate = useNavigate();

  /*functions*/
  const handleshow = ()=>{setshow(!show);}

  const conhandleshow = ()=>{setconshow(!conshow);}

  const submitHandler = async ()=>{
    setloading(true);
    if(!name || !email || !password || !confirmpassword)
{
  toast({
    title: 'Please fill all the feilds!',
    status: 'warning',
    duration: 5000,
    isClosable: true,
    position:'bottom'
  })
  setloading(!true);
  return
}
  if(password!==confirmpassword){
    toast({
      title: 'Passwords do not match',
      status: 'warning',
      duration: 5000,
      isClosable: true,
      position:'bottom'
    })
    setloading(!true);
  return
  }
    try {

      const {data} = await fetch("/api/user",{
        method:'POST',
        body:JSON.stringify({name,email,password,picture}),
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      })
      toast({
        title: 'Registration Sucessful',
        status: 'success',
        duration: 5000,
        isClosable: true,
        position:'bottom'
      })

      localStorage.setItem("userInfo",JSON.stringify(data))
      setloading(false)
      Navigate("/chats")
    } catch (error) {
      toast({
        title: 'error',
        status: 'error',
        duration: 5000,
        isClosable: true,
        position:'bottom'
      })
    }
    setloading(false)
  }

  const postDetails = async (pics)=>{
    setloading(true)
    if(pics===undefined){
      toast({
        title: 'Please Select an Image!',
        status: 'warning',
        duration: 5000,
        isClosable: true,
        position:'bottom'
      })
      return
    }
    if(pics.type === "image/jpeg" || pics.type=== "image/png"){
      const data = new FormData()
      data.append("file",pics)
      data.append("upload_preset","chat-app")
      data.append("cloud_name","doggnzlkv")
      try {
        const cloud = await fetch('https://api.cloudinary.com/v1_1/doggnzlkv/image/upload',{
        method:'post',
        body:data,
      })
      const finalpic = await cloud.json()
      const finalpic2 = finalpic.url.toString()
      setpicture(finalpic2)
      setloading(false)
      } catch (error) {
        setloading(false)
        toast({
          title: 'Please Select an Image!',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:'bottom'
        })
        return
    }
  }
  }


  return <VStack spacing={2}>
    <FormControl isRequired id='name'>
      <FormLabel fontSize='sm'>Name</FormLabel>
      <Input placeholder="Enter Your Name" onChange={(e)=>{setname(e.target.value)}} value={name}/>
    </FormControl>
    <FormControl isRequired id='emailsignup'>
      <FormLabel fontSize='sm'>Email</FormLabel>
      <Input type={'email'} placeholder="Enter Your Email" onChange={(e)=>{setemail(e.target.value)}} value={email}/>
    </FormControl>
    <FormControl isRequired id='passwordsignup'>
      <FormLabel fontSize='sm'>Password</FormLabel>
      <InputGroup>
      <Input type={show?'text':'password'} placeholder="Enter Your password" onChange={(e)=>{setpassword(e.target.value)}} value={password}/>
      <InputRightElement width="4.5rem">
        <Button h="1.75rem" size='sm' onClick={handleshow}>
          {show?'Hide':'Show'}
        </Button>
      </InputRightElement>
      </InputGroup>
    </FormControl> 
    <FormControl isRequired id='confirmpassword'>
      <FormLabel fontSize='sm'>Confirm Password</FormLabel>
      <InputGroup>
      <Input type={conshow?'text':'password'} placeholder="confirmpassword" onChange={(e)=>{setconfirmpassword(e.target.value)}} value={confirmpassword}/>
      <InputRightElement width="4.5rem">
        <Button h="1.75rem" size='sm' onClick={conhandleshow}>
          {conshow?'Hide':'Show'}
        </Button>
      </InputRightElement>
      </InputGroup>
    </FormControl> 
    <FormControl id='pic'>
      <FormLabel fontSize='sm'>Upload your Picture</FormLabel>
      <Input type={'file'}  p={2} accept="image/*" placeholder="Enter Yout Name" onChange={(e)=>{postDetails(e.target.files[0])}} />
    </FormControl> 

    <Button colorScheme='teal' width='100%' style={{marginTop:'20px',marginDown:'0px'}} 
      onClick={submitHandler} isLoading={loading}
  >Signup
</Button>
  </VStack>;
  
}
