import { ArrowBackIcon } from "@chakra-ui/icons";
import { Box, FormControl, IconButton, Input, Spinner, Text, useToast } from "@chakra-ui/react";
import {React,useState,useEffect} from "react";
import { ChatState } from "../../context/ChatProvider";
import { getSender,getSenderFull } from "../../ChatLogic";
import ProfileModel from "./ProfileModel"
import UpdateGroupChatModal from './UpdateGroupChatModal'
import ScrollableChat from "./ScrollableChat";
import io from "socket.io-client"

const ENDPOINT = "http://localhost:5000"
var socket,selectedchatcompare;

export default function SingleChat() {
  const [messages, setmessages] = useState([])
  const [loading, setloading] = useState(false)
  const [newmessage, setnewmessage] = useState("")
  const [socketconnected, setsocketconnected] = useState(false)
  const [istyping, setistyping] = useState(false)
  const [typing, settyping] = useState(false)

  const { user, selectedchat, setselectedchat,notification,setnotification,fetchagain,setfetchagain } =
    ChatState();
    const toast = useToast()

    useEffect(() => {
      socket = io(ENDPOINT)
      socket.emit("setup",user.user)
      socket.on('connected',()=>{
        setsocketconnected(true)
      })
      socket.on('typing',()=>setistyping(true))
      socket.on('stop typing',()=>setistyping(false))
      
    }, [])

    

  const sendMessage = async (event)=>{
    if(event.key==="Enter" && newmessage){
      socket.emit('stop typing',selectedchat._id)
        try {
          setnewmessage("");
          const data1 = await fetch('/api/message',{
            method:'POST',
            body: JSON.stringify({
              content:newmessage,
              chatId: selectedchat._id,
            }),
            headers:{
              authorization:`Bearer ${user.token}`,
              'Accept': 'application/json',
              
              'Content-Type': 'application/json'
            }
        })
        const data = await data1.json()
        
        
        setmessages([...messages,data])
        setloading(false)
        socket.emit('new message',data)
        } catch (error) {
          toast({
            title: 'Error Occurred!',
            description:"Failed to send the Message",
            status: 'error',
            duration: 3000,
            isClosable: true,
            position:'bottom-left'
          })
        }
    }
  }

  const typingHandler = (e)=>{
    setnewmessage(e.target.value)
    if(!socketconnected) return;

    if(!typing){
      settyping(true)
      socket.emit('typing',selectedchat._id)
    }
    
    let lastTypingTime = new Date().getTime();
    var timerLength = 3000;
    
    setTimeout(() => {
      
      var timeNow = new Date().getTime();
      var timeDiff = timeNow - lastTypingTime;
      if (timeDiff >= timerLength) {
        socket.emit("stop typing", selectedchat._id);
        settyping(false);
      }
    }, timerLength);
  }

  const fetchMessages = async ()=>{
    if(!selectedchat || selectedchat==="") return;
    setloading(true)
    try {
      const data1 = await fetch(`/api/message/${selectedchat._id}`,{
        method:'GET',
        headers:{
          authorization:`Bearer ${user.token}`,
          'Accept': 'application/json',
          
          'Content-Type': 'application/json'
        }
    })
    const data = await data1.json()
    /* console.log(data); */
    setmessages(data)
    setloading(false)
    socket.emit('join chat',selectedchat._id)
    } catch (error) {
      toast({
        title: 'Error Occurred!',
        description:"Failed to load Message",
        status: 'error',
        duration: 3000,
        isClosable: true,
        position:'bottom-left'
      })
    }
  }

  useEffect(() => {
    fetchMessages()

    selectedchatcompare = selectedchat
  }, [selectedchat])

  useEffect(() => {
    socket.on('message recieved',(newmessagerec)=>{
      if(!selectedchatcompare || selectedchatcompare._id!==newmessagerec.chat._id){
        if (!notification.includes(newmessagerec)) {
          setnotification([newmessagerec, ...notification]);
          setfetchagain(!fetchagain);
        }
      }
      else{
        setmessages([...messages,newmessagerec])
      }
    })
  })
  
  
  
  return (
    <>
      {selectedchat ? (
        <>
          <Text
            fontSize={{ base: "28px", md: "30px" }}
            pb={3}
            px={2}
            w="100%"
            fontFamily="Work sans"
            display="flex"
            justifyContent={{ base: "space-between" }}
            alignItems="center"
          >
            <IconButton
              display={{ base: "flex", md: "none" }}
              icon={<ArrowBackIcon />}
              onClick={() => setselectedchat("")}
            />
            {!selectedchat.isGroupChat ? (
              <>{getSender(user.user, selectedchat.users)}
              <ProfileModel user={getSenderFull(user.user, selectedchat.users)}/>
              </>
            ) : (
              <>{selectedchat.chatName.toUpperCase()}
              <UpdateGroupChatModal fetchMessages={fetchMessages}/>
              </>
            )}
          </Text>
          <Box display="flex"
          
          justifyContent="flex-end"
          flexDir="column" p={3} bg="#E8E8E8" w="100%" h="100%" borderRadius="lg" overflowY="hidden">
            {loading?<Spinner size="xl" w={20} h={20} alignSelf="center" margin="auto"/>:(<div display="flex" flexdir="column" overflowy="scroll" scrollboxwidth="none">
              <ScrollableChat messages={messages}/>
            </div>)}
            
            <FormControl onKeyDown={sendMessage} isRequired mt={3}>
              {istyping?<div>loading...</div>:<></>}
              <Input variant="filled" bg="#E0E0E0" placeholder="Enter a message.."onChange={typingHandler} 
              value={newmessage}/>
            </FormControl>
            
            
          </Box>
        </>
      ) : (
        <Box
          display="flex"
          alignItems="center"
          justifyContent="center"
          h="100%"
        >
          ,
          <Text fontSize="20px" pd={3} fontFamily="Work sans">
            <b>Click on a user to start chatting</b>
          </Text>
        </Box>
      )}
    </>
  );
}
