import { Box, IconButton, useDisclosure, useToast } from '@chakra-ui/react'
import React from 'react'
import { ChatState } from '../../context/ChatProvider'
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    Button,
    FormControl,
    Input
  } from '@chakra-ui/react'
  import UserListItem from "./UserListItem";
import { ViewIcon } from '@chakra-ui/icons'
import {useState} from "react"
import UserBadgeItem from "./UserBadgeItem";

function UpdateGroupChatModal({fetchMessages}) {

    const [groupchatname, setgroupchatname] = useState();
  const [search, setsearch] = useState("");
  const [searchresult, setsearchresult] = useState([]);
  const [loading, setloading] = useState(false);
  const [renameloading, setrenameloading] = useState(false)
  

  const toast = useToast()

    const {fetchagain, setfetchagain,user, selectedchat, setselectedchat} = ChatState()
    const { isOpen, onOpen, onClose } = useDisclosure()


    const handleRemove = async (user1)=>{
        if(selectedchat.groupAdmin._id!==user.user._id && user1._id!==user.user._id){
            toast({
                title: "Only adims can remove someone!",
                status: "error",
                duration: 3000,
                isClosable: true,
                position: "bottom",
              });
              return;
        }

        try {
            setloading(true)
            const dat = await fetch("/api/chat/groupremove", {
             method: "PUT",
             body:JSON.stringify({
                chatId:selectedchat._id,
                userId:user1._id
             }),
             headers: {
               authorization: `Bearer ${user.token}`,
               Accept: "application/json",
     
               "Content-Type": "application/json",
             },
           });
           const data = await dat.json()
 
           user1._id===user.user._id?setselectedchat():setselectedchat(data)
           setfetchagain(!fetchagain)
           fetchMessages()
           setloading(false)
 
         } catch (error) {
             toast({
                 title: "Error Ocurred!",
                 description:error.response.data.message,
                 status: "warning",
                 duration: 3000,
                 isClosable: true,
                 position: "bottom-left",
               });
               setloading(false)
         }
    }

    const handleRename = async ()=>{
        if(!groupchatname) return

        try {
            setrenameloading(true)
            const dat = await fetch("/api/chat/rename", {
                method: "PUT",
                body:JSON.stringify({
                   chatId:selectedchat._id,
                   chatName:groupchatname
                }),
                headers: {
                  authorization: `Bearer ${user.token}`,
                  Accept: "application/json",
        
                  "Content-Type": "application/json",
                },
              });
              const data = await dat.json()
              setselectedchat(data)
              setfetchagain(!fetchagain)
              setrenameloading(false)
        } catch (error) {
            toast({
                title: "Error Ocurred!",
                description:error.response.data.message,
                status: "warning",
                duration: 3000,
                isClosable: true,
                position: "bottom-left",
              });
              setrenameloading(false)
        }
        setgroupchatname("")
    }

    const handleSearch = async (query)=>{
        setsearch(query);
        if (!query) {
            setsearchresult([]);
          return;
        }
        try {
          setloading(true);
          const dat = await fetch(`/api/user?search=${search}`, {
            method: "GET",
            headers: {
              authorization: `Bearer ${user.token}`,
              Accept: "application/json",
    
              "Content-Type": "application/json",
            },
          });
          const data = await dat.json();
         
          setloading(false);
          setsearchresult(data);
        } catch (error) {
            toast({
                title: "Error Occured!",
                description: "Failed to load Search Results",
                status: "error",
                duration: 3000,
                isClosable: true,
                position: "bottom-left",
              });
        }
    }

    const handleAddUser = async (user1)=>{
        if(selectedchat.users.find((u)=> u._id===user1._id)){
            toast({
                title: "User Already in group!",
                status: "error",
                duration: 3000,
                isClosable: true,
                position: "bottom",
              });
              return;
        }
        if(selectedchat.groupAdmin._id!==user.user._id){
            toast({
                title: "only admins can add someone!",
                status: "error",
                duration: 3000,
                isClosable: true,
                position: "bottom",
              });
              return;
        }
        try {
           setloading(true)
           const dat = await fetch("/api/chat/groupadd", {
            method: "PUT",
            body:JSON.stringify({
               chatId:selectedchat._id,
               userId:user1._id
            }),
            headers: {
              authorization: `Bearer ${user.token}`,
              Accept: "application/json",
    
              "Content-Type": "application/json",
            },
          });
          const data = await dat.json()

          setselectedchat(data)
          setfetchagain(!fetchagain)
          setloading(false)

        } catch (error) {
            toast({
                title: "Error Ocurred!",
                description:error.response.data.message,
                status: "warning",
                duration: 3000,
                isClosable: true,
                position: "bottom-left",
              });
              setloading(false)
        }
    }

  return (
    <>
    <IconButton onClick={onOpen} display={{base:"flex"}} icon={<ViewIcon/>}/>

    <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader fontSize="35px" fontFamily="Work sans" display="flex" justifyContent="center">{selectedchat.chatName}</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <Box display="flex" flexWrap="wrap">
                {selectedchat.users.map((user)=>(<UserBadgeItem key={user._id} user={user} handleFunction={()=>handleRemove(user)} />))}
            </Box>
            <FormControl display="flex" flexWrap="wrap" >
              <Input
                placeholder="Update Group"
                mb={3}
                width="77%"
                
                onChange={(e) => setgroupchatname(e.target.value)}
              />
              <Button colorScheme='teal' variant="solid" ml={1} onClick={handleRename} isLoading={renameloading}>
              Update
            </Button>
            </FormControl>
            <FormControl>
              <Input
                placeholder="Add User to group"
                mb={3}
                onChange={(e) => handleSearch(e.target.value)}
              />
            </FormControl>
            {loading?<div>loading</div>:(searchresult.slice(0,4).map(user=>(<UserListItem key={user._id} user={user}handleFunction={()=>handleAddUser(user)}/>)))}
          </ModalBody>

          <ModalFooter>
            <Button colorScheme='red' mr={3} onClick={()=>{handleRemove(user)}}>
              Leave Group
            </Button>
            
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  )
}

export default UpdateGroupChatModal