import { Avatar, Box, Text,VStack } from "@chakra-ui/react";
import React from "react";

export default function UserListItem({ user, handleFunction }) {
  return (
    <div>
      <Box
        onClick={handleFunction}
        cursor="pointer"
        _hover={{
          background: "#38B2AC",
          color: "white",
        }}
        w="100%"
        display="flex"
        
        alignItems="center"
        color="black"
        px={3}
        py={2}
        mb={1}
        borderRadius="lg"
      >
        <Avatar
          mr={2}
          
          cursor="pointer"
          name={user.name}
          src={user.picture}
        />

        <VStack spacing={-1}
          align="stretch">
          <Text>
            {user.name}
          </Text>
          <Text>
            <b>Email:</b>
            {user.email}
          </Text>
    
        </VStack>
      </Box>
    </div>
  );
}
