import { CloseIcon } from "@chakra-ui/icons";
import { Box } from "@chakra-ui/react";
import React from "react";

export default function UserBadgeItem({ user, handleFunction }) {
  return (
    <Box
      onClick={handleFunction}
      cursor="pointer"
     _hover={{
        fontSize:"18px"
      }}
      fontSize={12}
      backgroundColor="pink"

      variant="solid"
      alignItems="center"
      px={3}
      py={2}
      mb={2}
      m={1}
      borderRadius="lg"
    >
        {user.name}
        <CloseIcon pl={1}/>
    </Box>
  );
}
